html {
  font-family: 'Courier New', Courier, monospace;
}

.select-search__option {
  display: block;
  height: 50px;
  width: 100%;
  padding: 0 16px;
  background: white;
  border: none;
  outline: none;
  font-size: 25px;
  text-align: left;
}

.side-by-side-container {
  display: inline-block;
  vertical-align: top;
  max-width: 600px;
  margin: 10px;
}

.container {
  margin: 5px;
  padding: 5px;
  background-color: lightgrey;
  border-radius: 5px;
  padding-bottom: 20px;
}

.word {
  background-color: white;
  border: black;
  border-style: solid;
  border-width: thin;
  border-color: grey;
  margin: 0px;
  border-radius: 2px;
  display: inline-block;
  padding: 2px;
}

.emoji-span {
  font-size: 80px;
}